	module.exports = "<main id=\"content\" role=\"main\" tabindex=\"-1\">\n  <h1 class=\"heading-xlarge\">\n    <span class=\"heading-secondary\">Content</span>\n    Buttons\n  </h1>\n\n  <p class=\"lede text\">\n    Use buttons to move though a transaction, aim to use only one button per page.\n  </p>\n\n  <h2 class=\"heading-small heading-contents\">Contents:</h2>\n  <div class=\"grid-row\">\n    <div class=\"column-two-thirds\">\n      <ul class=\"list list-contents\">\n        <li><a ui-sref=\"content.buttons({'#':'button-text'})\" href=\"/#/content/buttons\">Button text</a></li>\n        <li><a ui-sref=\"content.buttons({'#':'button-start-now'})\">Start now button</a></li>\n        <li><a ui-sref=\"content.buttons({'#':'button-alignment'})\">Button alignment</a></li>\n        <li><a ui-sref=\"content.buttons({'#':'button-disabled'})\">Disabled buttons</a></li>\n        <li><a ui-sref=\"content.buttons({'#':'creating-buttons'})\">Creating buttons</a></li>\n      </ul>\n    </div>\n  </div>\n\n  <h3 class=\"heading-medium\" id=\"button-text\">Button text</h3>\n  <p class=\"text\">\n    Button text should be short and describe the action the button performs.\n  </p>\n\n  <div class=\"example\">\n    <input class=\"button\" type=\"submit\" value=\"Save and continue\">\n  </div>\n\n<prismify language=\"markup\">\n  <input class=\"button\" type=\"submit\" value=\"Save and continue\">\n</prismify>\n\n  <h3 class=\"heading-medium\" id=\"button-start-now\">Start now button</h3>\n  <p class=\"text\">\n    Launch your service with a Start now button.\n  </p>\n\n  <div class=\"example\">\n    <a class=\"button button-start\" href=\"#\" role=\"button\">Start now</a>\n  </div>\n\n<prismify language=\"markup\">\n  <a class=\"button button-start\" href=\"#\" role=\"button\">Start now</a>\n</prismify>\n\n  <h3 class=\"heading-medium\" id=\"button-alignment\">Button alignment</h3>\n  <p class=\"text\">\n    Align the primary action button to the left edge of your form, in the user's line of sight.\n  </p>\n\n  <div class=\"example example-button\">\n\n    <form action=\"/\" method=\"post\" class=\"form\">\n      <div class=\"form-group\">\n        <label for=\"form-email-address-1\" class=\"form-label\">Email address</label>\n        <input type=\"text\" class=\"form-control\" id=\"form-email-address-1\">\n      </div>\n      <div class=\"form-group\">\n        <button class=\"button\">Save and continue</button>\n      </div>\n    </form>\n\n  </div>\n\n  <h3 class=\"heading-medium\" id=\"button-disabled\">Disabled buttons</h3>\n  <p>\n    Disabled buttons should be set at 50% opacity.\n  </p>\n\n  <div class=\"example\">\n    <button class=\"button\" disabled=\"disabled\">Primary button</button>\n  </div>\n  \n  <h3 class=\"heading-medium\" id=\"button-secondary\">Secondary buttons</h3>\n  <p>\n    The secondary button should be used sparingly.\n  </p>\n\n  <div class=\"example\">\n    <input class=\"button button-secondary\" type=\"submit\" value=\"Secondary button\">\n  </div>\n\n  <h3 class=\"heading-medium\" id=\"button-tertiary\">Tertiary buttons</h3>\n  <p>\n    The tertiary button is used only by IdAM.\n  </p>\n\n  <div class=\"example\">\n    <input class=\"button button-tertiary\" type=\"submit\" value=\"Tertiary button\">\n  </div>\n\n<prismify language=\"markup\">\n  <input class=\"button\" type=\"submit\" value=\"Save and continue\" disabled=\"disabled\">\n</prismify>\n\n  <h3 class=\"heading-medium\" id=\"creating-buttons\">Creating buttons</h3>\n  <p class=\"text\">\n    Use the GOV.UK Sass button mixin &ndash; find this in the GOV.UK frontend toolkit <a href=\"https://github.com/alphagov/govuk_frontend_toolkit/blob/master/stylesheets/design-patterns/_buttons.scss\" rel=\"external\">buttons.scss</a> file.\n  </p>\n\n  <div class=\"example example-button\">\n    <div class=\"grid-row\">\n      <div class=\"column-one-third\">\n\n        <button class=\"button\">Save and continue</button>\n\n      </div>\n      <div class=\"column-one-third\">\n\n        <div class=\"swatch-wrapper\">\n          <h4 class=\"heading-small\">Button</h4>\n          <div class=\"swatch swatch-button-colour\"></div>\n          <ul>\n            <li><span class=\"bold-xsmall\">#00823B</span></li>\n            <li>$button-colour</li>\n          </ul>\n        </div>\n\n      </div>\n      <div class=\"column-one-third\">\n\n        <div class=\"swatch-wrapper\">\n          <h4 class=\"heading-small\">Focus</h4>\n          <div class=\"swatch swatch-yellow\"></div>\n          <ul>\n            <li><span class=\"bold-xsmall\">#FFBF47</span></li>\n            <li>$focus-colour</li>\n          </ul>\n        </div>\n\n      </div>\n\n    </div>\n  </div>\n</main><!-- / #content -->";

