	module.exports = "<h1 class=\"heading-xlarge\">\n  <span class=\"heading-secondary\">Single Page Platform Development Kit</span>\n  Content\n</h1>\n<div class=\"text\">\n  <p class=\"lede\">\n    This guide shows how to make your service look consistent with the rest of GOV.UK.\n  </p>\n</div>\n\n<div class=\"grid-row\">\n  <div class=\"column-one-third\">\n    <h2 class=\"heading-medium\"><a ui-sref=\"content.typography\">Typography</a></h2>\n    <p>\n      Headings, body text, links, lists, inset text, hidden text.\n    </p>\n  </div>\n  <div class=\"column-one-third\">\n    <h2 class=\"heading-medium\"><a ui-sref=\"content.colours\">Colour</a></h2>\n    <p>\n      Colour contrast, Sass variables, colour palettes.\n    </p>\n  </div>\n  <div class=\"column-one-third\">\n    <h2 class=\"heading-medium\"><a ui-sref=\"content.icons-media\">Icons and media</a></h2>\n    <p>\n      Icons and image ratios.\n    </p>\n  </div>\n</div>\n<div class=\"grid-row\">\n  <div class=\"column-one-third\">\n    <h2 class=\"heading-medium\"><a ui-sref=\"content.buttons\">Buttons</a></h2>\n    <p>\n      Button text, button alignment, creating buttons.\n    </p>\n  </div>\n  <div class=\"column-one-third\">\n    <h2 class=\"heading-medium\"><a ui-sref=\"content.form-elements\">Form elements</a></h2>\n    <p>\n      Form fields, labels, focus states, radio buttons, checkboxes.\n    </p>\n  </div>\n  <div class=\"column-one-third\">\n    <h2 class=\"heading-medium\"><a ui-sref=\"content.errors\">Errors and validation</a></h2>\n    <p>\n      Summary boxes, highlighting errors in forms.\n    </p>\n  </div>\n</div>\n<div class=\"grid-row\">\n  <div class=\"column-one-third\">\n    <h2 class=\"heading-medium\"><a ui-sref=\"content.utils\">Utility classes</a></h2>\n    <p>\n      Floating, clearing, hidding and showing content classes.\n    </p>\n  </div>\n  <div class=\"column-one-third\">\n    <h2 class=\"heading-medium\"><a ui-sref=\"content.data\">Data</a></h2>\n    <p>\n      Data in a table, numeric tabular data, data visualisation.\n    </p>\n  </div>  \n  <div class=\"column-one-third\">\n    <h2 class=\"heading-medium\"><a ui-sref=\"content.helper-classes\">Helper Classes</a></h2>\n    <p>\n      Margins, floating, clearing, hiding and showing content classes.\n    </p>\n  </div>\n</div>";

