	module.exports = "<main id=\"content\" role=\"main\">\n\n  <p class=\"lede text\">\n    Avoid unnecessary decoration - only use icons and images if there's a real user need.\n  </p>\n\n  <h2 class=\"heading-small heading-contents\">Contents:</h2>\n  <div class=\"grid-row\">\n    <div class=\"column-two-thirds\">\n      <ul class=\"list list-contents\">\n        <li><a href=\"#icons\">Icons</a></li>\n        <li><a href=\"#images\">Images</a></li>\n        <li><a href=\"#examples\">Examples</a></li>\n      </ul>\n    </div>\n  </div>\n\n  <h3 class=\"heading-medium\" id=\"icons\">Icons</h3>\n  <ul class=\"list list-bullet text\">\n    <li>\n      if icons are needed ensure they are clear, simple and accompanied by relevant text\n    </li>\n    <li>\n      dont hide functionality under icons\n    </li>\n  </ul>\n\n  <p class=\"text\">\n    GOV.UK standard icons are provided, as well as organisation crests and media player icons  find these in the GOV.UK frontend toolkit <a href=\"https://github.com/alphagov/govuk_frontend_toolkit/tree/master/images\" rel=\"external\">images folder</a>.\n  </p>\n\n  <h4 class=\"heading-small\">Legal text</h4>\n  <p class=\"text\">\n    Use bold text with an exclamation icon if there are legal consequences - for example, a fine or prison sentence.\n  </p>\n\n\n<docs-example markup>\n  <div class=\"notice\">\n      <i class=\"icon icon-important\">\n        <span class=\"visuallyhidden\">Warning</span>\n      </i>\n      <strong class=\"bold-small\">\n        You can be fined up to 5,000 if you dont register.\n      </strong>\n  </div>\n</docs-example>\n\n  <h3 class=\"heading-medium\" id=\"images\">Images</h3>\n  <p>\n    If images are needed they should be landscape, 3:2 aspect ratio.\n  </p>\n\n  <div class=\"example example-images\">\n    <div class=\"grid-row\">\n      <div class=\"column-one-half\">\n        <img src=\"http://govuk-elements.herokuapp.com/public/images/examples/3by2.jpg\" alt=\"3:2\">\n      </div>\n      <div class=\"column-one-half\">\n        <img src=\"http://govuk-elements.herokuapp.com/public/images/examples/pm.jpg\" alt=\"\">\n      </div>\n    </div>\n  </div>\n</main>";

