	module.exports = "<main id=\"content\" role=\"main\" tabindex=\"-1\">\n\n  <h1 class=\"heading-xlarge\">\n    <span class=\"heading-secondary\">Layout</span>\n    Citizen\n  </h1>\n\n  <div class=\"text\">\n    <p class=\"lede\">\n      Use white space to create a visual hierarchy on the page.\n    </p>\n  </div>\n\n  <h2 class=\"heading-small heading-contents\">Contents:</h2>\n  <div class=\"grid-row\">\n    <div class=\"column-two-thirds\">\n      <ul class=\"list list-contents\">\n        <li><a ui-sref=\"layout.citizen({'#':'page-width'})\">Page width</a></li>\n        <li><a ui-sref=\"layout.citizen({'#':'screen-size'})\">Screen size</a></li>\n        <li><a ui-sref=\"layout.citizen({'#':'layout-spacing'})\">Spacing</a></li>\n        <li><a ui-sref=\"layout.citizen({'#':'layout-grid-unit-proportions'})\">Grid unit proportions</a></li>\n        <li><a ui-sref=\"layout.citizen({'#':'examples'})\">Examples</a></li>\n      </ul>\n    </div>\n  </div>\n\n  <h3 class=\"heading-medium\" id=\"page-width\">Page width</h3>\n  <div class=\"text\">\n    <p>\n      The default maximum page width is 1020px, but go wider if the content requires it.\n    </p>\n    <p>\n      <a href=\"#layout-grid-unit-proportions\">Use a grid</a> to lay out your content. To prevent long lines of text, content should sit in a column which is two-thirds of the page width.\n    </p>\n    <p>\n      Long lines reduce legibility, so all lines of text should be no longer than 70 to 80 characters.\n    </p>\n  </div>\n\n  <h3 class=\"heading-medium\" id=\"screen-size\">Screen size</h3>\n  <div class=\"text\">\n    <p>\n      Design for small screens first using a single column layout.\n    </p>\n    <p>\n      Optimise for different screen sizes, but dont make assumptions about what devices people are using.\n    </p>\n  </div>\n\n  <h3 class=\"heading-medium\" id=\"layout-spacing\">Spacing</h3>\n  <ul class=\"list list-bullet text\">\n    <li>spacing between elements is usually 5px, 10px, 15px or multiples of 15px</li>\n    <li>gutters are 15px for smaller screens and 30px for larger screens</li>\n  </ul>\n\n\n  <div class=\"example example-images\">\n    <div class=\"grid-row\">\n      <div class=\"column-one-half\">\n        <img src=\"http://govuk-elements.herokuapp.com/public/images/examples/15px-gutter-example.png\" alt=\"15px gutter example\">\n      </div>\n      <div class=\"column-one-half\">\n        <img src=\"http://govuk-elements.herokuapp.com/public/images/examples/30px-gutter-example.png\" alt=\"30px gutter example\">\n      </div>\n    </div>\n  </div>\n\n  <h3 class=\"heading-medium\" id=\"layout-grid-unit-proportions\">Grid unit proportions</h3>\n  <ul class=\"list list-bullet text\">\n    <li>introduce columns as the content requires it  base column ratios on halves, thirds or quarters of the page width</li>\n    <li>for screen breakpoints use media queries &ndash; find these in the GOV.UK frontend toolkit <a href=\"https://github.com/alphagov/govuk_frontend_toolkit/blob/master/stylesheets/_conditionals.scss\">_conditionals.scss</a> file</li>\n  </ul>\n\n  <h4 class=\"heading-medium\">Halves</h4>\n  <div class=\"example example-grid\">\n    <div class=\"grid-row\">\n\n      <div class=\"column-one-half\">\n        <p>Content</p>\n      </div>\n\n      <div class=\"column-one-half\">\n        <p>Content</p>\n      </div>\n\n    </div>\n  </div>\n\n<prismify language=\"markup\">\n  <div class=\"grid-row\">\n\n    <div class=\"column-one-half\">\n      <p>Content</p>\n    </div>\n\n    <div class=\"column-one-half\">\n      <p>Content</p>\n    </div>\n\n  </div>\n</prismify language=\"markup\">\n\n  <h4 class=\"heading-medium\">Thirds</h4>\n  <div class=\"example example-grid\">\n    <div class=\"grid-row\">\n\n      <div class=\"column-one-third\">\n        <p>Content</p>\n      </div>\n\n      <div class=\"column-one-third\">\n        <p>Content</p>\n      </div>\n\n      <div class=\"column-one-third\">\n        <p>Content</p>\n      </div>\n\n    </div>\n  </div>\n\n<prismify language=\"markup\">\n  <div class=\"grid-row\">\n\n    <div class=\"column-one-third\">\n      <p>Content</p>\n    </div>\n\n    <div class=\"column-one-third\">\n      <p>Content</p>\n    </div>\n\n    <div class=\"column-one-third\">\n      <p>Content</p>\n    </div>\n\n  </div>\n</prismify language=\"markup\">\n\n  <h4 class=\"heading-medium\">Two thirds / One third</h4>\n  <div class=\"example example-grid\">\n    <div class=\"grid-row\">\n\n      <div class=\"column-two-thirds\">\n        <p>Content</p>\n      </div>\n\n      <div class=\"column-one-third\">\n        <p>Content</p>\n      </div>\n\n    </div>\n  </div>\n\n<prismify language=\"markup\">\n  <div class=\"grid-row\">\n\n    <div class=\"column-two-thirds\">\n      <p>Content</p>\n    </div>\n\n    <div class=\"column-one-third\">\n      <p>Content</p>\n    </div>\n\n  </div>\n</prismify language=\"markup\">\n\n  <h4 class=\"heading-medium\">One third / Two thirds</h4>\n  <div class=\"example example-grid\">\n    <div class=\"grid-row\">\n\n      <div class=\"column-one-third\">\n        <p>Content</p>\n      </div>\n\n      <div class=\"column-two-thirds\">\n        <p>Content</p>\n      </div>\n\n    </div>\n  </div>\n\n<prismify language=\"markup\">\n  <div class=\"grid-row\">\n\n    <div class=\"column-one-third\">\n      <p>Content</p>\n    </div>\n\n    <div class=\"column-two-thirds\">\n      <p>Content</p>\n    </div>\n\n  </div>\n</prismify language=\"markup\">\n\n  <h4 class=\"heading-medium\">Quarters</h4>\n  <div class=\"example example-grid\">\n    <div class=\"grid-row\">\n\n      <div class=\"column-one-quarter\">\n        <p>Content</p>\n      </div>\n\n      <div class=\"column-one-quarter\">\n        <p>Content</p>\n      </div>\n\n      <div class=\"column-one-quarter\">\n        <p>Content</p>\n      </div>\n\n      <div class=\"column-one-quarter\">\n        <p>Content</p>\n      </div>\n\n    </div>\n  </div>\n\n<prismify language=\"markup\">\n  <div class=\"grid-row\">\n\n    <div class=\"column-one-quarter\">\n      <p>Content</p>\n    </div>\n\n    <div class=\"column-one-quarter\">\n      <p>Content</p>\n    </div>\n\n    <div class=\"column-one-quarter\">\n      <p>Content</p>\n    </div>\n\n    <div class=\"column-one-quarter\">\n      <p>Content</p>\n    </div>\n\n  </div>\n</prismify language=\"markup\">\n</main><!-- / #content -->";

