	module.exports = "<h3 class=\"heading-medium mb-l\">Alerts</h3>\n\n<div class=\"grid-row\">\n\n  <div class=\"column-one-third font-xsmall\">\n    <p>The <b>pdk-alert</b> component is used for displaying messages of\n    high importance.</p>\n\n    <h3 class=\"heading-small mb-m\">Configuration</h3>\n    <ul class=\"list list-docs\">\n      <li>\n        <code class=\"code\">banner</code> <ng-badge></ng-badge>\n        <em class=\"text-secondary\">(Default: false)</em>\n        A boolean expression for displaying the alert so that it spans the width of the viewport.\n      </li>\n      <li>\n        <code class=\"code\">banner-class</code>\n        <em class=\"text-secondary\">(Optional)</em>\n        A container class for the banner content. This should be specified when\n        a page's content does not use the conventional GDS width.\n      </li>\n      <li>\n        <code class=\"code\">show-icon</code> <ng-badge></ng-badge>\n        <em class=\"text-secondary\">(Default: true)</em>\n        A boolean value for setting for whether the accompanying icon be displayed.\n      </li>\n      <li>\n        <p><code class=\"code\">type</code> The context of the notification:</p>\n        <ul class=\"list\">\n          <li>\n            <span class=\"docs-swatch swatch-red\"></span>\n            <span class=\"docs-swatch-label\">warning</span>\n          </li>\n          <li>\n            <span class=\"docs-swatch swatch-turquoise\"></span>\n            <span class=\"docs-swatch-label\">confirmation / success</span>\n          </li>\n        </ul>\n      </li>\n    </ul>\n  </div>\n\n  <div class=\"column-two-thirds\">\n    <docs-example data-language=\"markup\">\n      <pdk-alert type=\"warning\">\n        The requested resource could not be found. <a href=\"\">Try again</a>\n      </pdk-alert>\n    </docs-example>\n  </div>\n</div>";

