	module.exports = "<h2>Notification</h2>\n\n<gov-tabset>\n\n  <gov-tab heading=\"HTML Example\">\n\n    <section aria-labelledby=\"notifications\">\n    \n      <h3 class=\"heading-medium\" id=\"notifications\">Notifications</h3> \n      \n      <ul class=\"text list list-bullet\">\n        <li>no full stop should be used for notifications</li>\n        <li>top of the page just below the breadcrumb or phase banner</li>\n        <li>can be used with or without icon</li>\n      </ul>\n      \n      <p>Critical and non&ndash;critical notifications</p>\n      <div docs-example language=\"markup\">\n        <div class=\"notification notification-confirmation\" role=\"alert\">\n          <i class=\"icon icon-tick-white\" role=\"presentation\" aria-hidden=\"true\"></i>\n          <div class=\"notification-message\">\n            <p class=\"bold-medium\">Accepted case management information</p>\n          </div>\n        </div>\n        <div class=\"notification notification-confirmation\" role=\"alert\">\n          <div class=\"notification-message\">\n            <p class=\"bold-medium\">Accepted case management information</p>\n          </div>\n        </div>\n      </div>\n      \n      <div class=\"panel panel-border-wide\">\n        <p>This pattern is for <b>IdAM</b> only.</p>\n      </div>\n      \n      <p>Active notifications</p>\n      <docs-example language=\"markup\">\n        <div class=\"notification notification-active\" role=\"alert\">\n          <i class=\"icon icon-tick-white\" role=\"presentation\" aria-hidden=\"true\"></i>\n          <div class=\"notification-message\">\n            <p class=\"bold-medium\">With optional tick icon and text wrapping lorem ipsum dolor sit amet, consectetur adipiscing elit sed do tem por incididunt ut labore et dolore magna aliqua</p>\n          </div>\n        </div>\n        <p>Pending notifications</p>\n        <div class=\"notification notification-pending\" role=\"alert\">\n          <div class=\"notification-message\">\n            <p class=\"bold-medium\">Text only with no icon</p>\n          </div>\n        </div>\n        <p>Locked notifications</p>\n        <div class=\"notification notification-locked\" role=\"alert\">       \n          <i class=\"icon icon-important-white\" role=\"presentation\" aria-hidden=\"true\"></i>\n          <div class=\"notification-message\">\n            <p class=\"bold-medium\">This users mobile number has not yet been verified</p>\n          </div>\n        </div>\n        <p>Deregistered notifications</p>\n        <div class=\"notification notification-deregistered\" role=\"alert\">\n          <div class=\"notification-message\">\n            <p class=\"bold-medium\">Example grey notification</p>\n          </div>\n        </div>\n      </docs-example>\n    </section> \n    <p class=\"font-xsmall\"><a href=\"#\">Back to the top</a></p>\n  </gov-tab>\n\n  <gov-tab heading=\"Angular Markup\">\n    <docs-example language=\"markup\">\n      <p>Critical and noncritical notifications</p>\n      <gov-notification \n        type=\"confirmation\" \n        show-icon=\"true\">\n        Accepted case management information\n      </gov-notification>\n      <gov-notification \n        type=\"confirmation\" \n        show-icon=\"false\">\n        Accepted case management information\n      </gov-notification>\n      <gov-notification\n          type=\"confirmation\"\n          link=\"$ctrl.link\"\n          show-icon=\"true\">\n        Accepted case management information\n      </gov-notification>\n\n      <p>Active notifications</p>\n      <gov-notification \n        type=\"active\" \n        show-icon=\"true\">\n        With optional tick icon and text wrapping lorem ipsum dolor sit amet, consectetur adipiscing elit sed do tem por incididunt ut labore et dolore magna aliqua\n      </gov-notification>            \n      <p>Pending notifications</p>\n      <gov-notification \n        type=\"pending\" \n        show-icon=\"true\">\n        Text only with no icon\n      </gov-notification> \n      <p>Locked notifications</p>\n      <gov-notification \n        type=\"locked\" \n        show-icon=\"true\">\n        This users mobile number has not yet been verified\n      </gov-notification>      \n      <p>Deregistered notifications</p>\n      <gov-notification \n        type=\"deregistered\" \n        show-icon=\"true\">\n        Example grey notification\n      </gov-notification>\n    </docs-example>\n  </gov-tab>\n\n  <gov-tab heading=\"Data\">\n    <docs-example language=\"javascript\">\n      examples = [\n        {showIcon: true,  style: 'success'},\n        {showIcon: false, style: 'invalid'}\n      ];\n\n      link = {\n        url: 'https://direct.gov.uk',\n        title: 'Check Gov.uk'\n      }\n    </docs-example>\n  </gov-tab>\n\n</gov-tabset>\n\n<discuss-component issue=\"20\"></discuss-component>";

