	module.exports = "<h3 class=\"heading-medium mb-l\">Summary Item</h3>\n\n<div class=\"grid-row\">\n\n  <div class=\"column-one-third font-xsmall\">\n    The <b>pdk-summary-item</b> component is used for displaying data as featured content.\n\n    <h3 class=\"heading-small mb-m\">Configuration</h3>\n\n    <ul class=\"list list-docs\">\n      <li>\n        <code class=\"code\">hero</code> <ng-badge></ng-badge>\n        <em class=\"text-secondary\">(Default: false)</em>\n        A boolean value for displaying a large version of the data.\n      </li>\n      <li>\n        <code class=\"code\">inline</code> <ng-badge></ng-badge>\n        <em class=\"text-secondary\">(Default: false)</em>\n        A boolean value for displaying the label alongside the count\n        rather than below it.\n      </li>\n      <li>\n        <code class=\"code\">count</code> <ng-badge></ng-badge>\n        The count of the items of being summarized.\n      </li>\n      <li>\n        <code class=\"code\">label</code> <ng-badge></ng-badge>\n        The label for the items being summarized.\n      </li>\n    </ul>\n  </div>\n\n  <div class=\"column-two-thirds\">\n    <docs-example data-language=\"markup\">\n      <div class=\"grid-row\">\n        <div class=\"column-third\">\n          <a href=\"\">\n            <pdk-summary-item count=\"5\" label=\"Statements\"></pdk-summary-item>\n          </a>\n        </div>\n        <div class=\"column-third\">\n          <pdk-summary-item hero=\"true\" count=\"8\" inline=\"true\" label=\"Files\">\n          </pdk-summary-item>\n        </div>\n        <div class=\"column-third\">\n          <pdk-summary-item count=\"22\" inline=\"true\" label=\"Exhibits\">\n          </pdk-summary-item>\n        </div>\n      </div>\n    </docs-example>\n  </div>\n</div>";

