	module.exports = "<h2 class=\"heading-medium\">Tab</h2>\n<p>ARIA attributes are being added and switched using JavaScript.</p>\n<p>This component accepts 2 extra options. A defaultIndex which will by default pre-select the tab desired and\na onClick method that will be called when the tab is selected, allowing to do aynchronous actions.</p>\n\n<docs-example language=\"markup\">\n  <gov-tabset>\n    <gov-tab heading=\"Before you start\">\n        <h3 class=\"heading-medium\">Before you start</h3>\n        <div class=\"text\">\n          <p>To get your first provisional driving licence online for a car, motorcycle or moped you must:</p>\n          <ul class=\"list list-bullet\">\n            <li>be a resident of Great Britain  theres a different service in <a href=\"http://www.nidirect.gov.uk/applying-for-a-provisional-driving-licence\" rel=\"external\">Northern Ireland</a></li>\n            <li>meet the minimum age requirement</li>\n            <li>meet the <a href=\"/driving-eyesight-rules\">minimum eyesight requirement</a></li>\n            <li>not be prevented from driving for any reason</li>\n            <li>pay 50 by MasterCard, Visa, Electron, Maestro or Delta debit or credit card</li>\n            <li>have a valid <abbr title=\"United Kngdom\">UK</abbr> passport or <a href=\"/driving-licence-application-identity-documents\">other form of identity</a></li>\n            <li>have your National Insurance number if known</li>\n            <li>provide addresses where youve lived for the last 3 years</li>\n          </ul>\n        </div>\n    </gov-tab>\n    <gov-tab heading=\"Need to know\">\n      <h3 class=\"heading-medium\">Need to know</h3>\n      <div class=\"text\">\n        <ul class=\"list list-bullet\">\n          <li>proof of identification required</li>\n          <li>3 years of address details required</li>\n          <li>debit or credit card required</li>\n        </ul>\n      </div>\n    </gov-tab>\n    <gov-tab heading=\"Ways to apply\" data-on-click=\"$ctrl.test()\">\n      <h3 class=\"heading-medium\" tabindex=\"0\">Ways to apply</h3>\n      <div class=\"text\">\n        <p>You can apply by post by completing a D1 application form, which you can get from the <a href=\"#\"><abbr title=\"Driver and Vehicle Licensing Agency\">DVLA</abbr> form ordering service</a> or from a Post Office.</p>\n        <p>Youll also need to include:</p>\n        <ul class=\"list list-bullet\">\n          <li>original documentation confirming your identity</li>\n          <li>a colour <a href=\"/photos-for-passports\">passportstyle photograph</a></li>\n          <li>the fee of 50 by cheque or postal order payable to <abbr title=\"Driver and Vehicle Licensing Agency\">DVLA</abbr> (do not send cash)</li>\n        </ul>\n        <p>Send your completed application and payment to <abbr title=\"Driver and Vehicle Licensing Agency\">DVLA</abbr>, Swansea, SA99 1AD.</p>\n        <p>If youre including non<abbr title=\"United Kingdom\">UK</abbr> identity documents send your application and payment to <abbr title=\"Driver and Vehicle Licensing Agency\">DVLA</abbr>, Swansea, SA99 1AF.</p>\n      </div>\n    </gov-tab>\n  </gov-tabset>\n</docs-example>\n\n<p>Tabs can used horizontally by default or vertically adding a flag in the component <code>vertical=\"true\"</code></p>\n\n<docs-example language=\"markup\">\n  <gov-tabset data-vertical=\"true\">\n    <gov-tab heading=\"Before you start\">\n      <h3 class=\"heading-medium\">Before you start</h3>\n      <div class=\"text\">\n        <p>To get your first provisional driving licence online for a car, motorcycle or moped you must:</p>\n        <ul class=\"list list-bullet\">\n          <li>be a resident of Great Britain  theres a different service in <a href=\"http://www.nidirect.gov.uk/applying-for-a-provisional-driving-licence\" rel=\"external\">Northern Ireland</a></li>\n          <li>meet the minimum age requirement</li>\n          <li>meet the <a href=\"/driving-eyesight-rules\">minimum eyesight requirement</a></li>\n          <li>not be prevented from driving for any reason</li>\n          <li>pay 50 by MasterCard, Visa, Electron, Maestro or Delta debit or credit card</li>\n          <li>have a valid <abbr title=\"United Kngdom\">UK</abbr> passport or <a href=\"/driving-licence-application-identity-documents\">other form of identity</a></li>\n          <li>have your National Insurance number if known</li>\n          <li>provide addresses where youve lived for the last 3 years</li>\n        </ul>\n      </div>\n    </gov-tab>\n    <gov-tab heading=\"Need to know\">\n      <h3 class=\"heading-medium\">Need to know</h3>\n      <div class=\"text\">\n        <ul class=\"list list-bullet\">\n          <li>proof of identification required</li>\n          <li>3 years of address details required</li>\n          <li>debit or credit card required</li>\n        </ul>\n      </div>\n    </gov-tab>\n    <gov-tab heading=\"Ways to apply\" data-on-click=\"$ctrl.test()\">\n      <h3 class=\"heading-medium\" tabindex=\"0\">Ways to apply</h3>\n      <div class=\"text\">\n        <p>You can apply by post by completing a D1 application form, which you can get from the <a href=\"#\"><abbr title=\"Driver and Vehicle Licensing Agency\">DVLA</abbr> form ordering service</a> or from a Post Office.</p>\n        <p>Youll also need to include:</p>\n        <ul class=\"list list-bullet\">\n          <li>original documentation confirming your identity</li>\n          <li>a colour <a href=\"/photos-for-passports\">passportstyle photograph</a></li>\n          <li>the fee of 50 by cheque or postal order payable to <abbr title=\"Driver and Vehicle Licensing Agency\">DVLA</abbr> (do not send cash)</li>\n        </ul>\n        <p>Send your completed application and payment to <abbr title=\"Driver and Vehicle Licensing Agency\">DVLA</abbr>, Swansea, SA99 1AD.</p>\n        <p>If youre including non<abbr title=\"United Kingdom\">UK</abbr> identity documents send your application and payment to <abbr title=\"Driver and Vehicle Licensing Agency\">DVLA</abbr>, Swansea, SA99 1AF.</p>\n      </div>\n    </gov-tab>\n  </gov-tabset>\n</docs-example>\n\n<discuss-component issue=\"18\"></discuss-component>";

