	module.exports = "<h2 class=\"heading-medium\">Search Input & Results</h2>\n<p class=\"text\">This components will display a search input and the number of search results found. The angular example below hides the search results until the user triggers a valid search action. The search result is hard coded to return static data.</p>\n\n\n<gov-tabset>\n\n  <gov-tab heading=\"HTML Example\">\n    <docs-example language=\"markup\">\n      <div class=\"form-search\">\n        <label class=\"visually-hidden\" for=\"search\">Label</label>\n        <input class=\"form-control\" type=\"text\" id=\"inputId\" placeholder=\"Search\"\n              name=\"inputName\" autocomplete=\"true\"\n              aria-describedby=\"Aria  description\" required=\"true\"><button class=\"form-search-button\" type=\"submit\" role=\"button\" type=\"submit\">Search</button>\n      </div>\n      <div class=\"panel panel-border-wide\">\n        <p>0 results found</p>\n      </div>       \n    </docs-example>   \n  </gov-tab>\n\n  <gov-tab heading=\"Angular Markup\">\n    <docs-example language=\"markup\">\n      <gov-search  id=\"$ctrl.inputId\"\n          name=\"$ctrl.search\" autocomplete=\"true\" ng-model=\"$ctrl.model\" aria-describedby=\"$ctrl.ariaDescribedBy\" ng-minlength=\"$ctrl.ngMinLength\" placeholder=\"Search\"\n          label=\"$ctrl.label\" on-search=\"$ctrl.onSearch($ctrl.model)\" required=\"required\" results=\"$ctrl.results\"></gov-search>\n    </docs-example>\n    <p>Example displaying the label</p>\n    <docs-example language=\"markup\">\n      <gov-search  id=\"$ctrl.inputId\"\n                   name=\"$ctrl.search\" autocomplete=\"true\" ng-model=\"$ctrl.model\" aria-describedby=\"$ctrl.ariaDescribedBy\" ng-minlength=\"$ctrl.ngMinLength\"\n                   label=\"{{$ctrl.label}}\" data-show-label=\"true\" on-search=\"$ctrl.onSearch($ctrl.model)\" required=\"required\" results=\"$ctrl.results\"></gov-search>\n\n    </docs-example>\n  </gov-tab>\n\n  <gov-tab heading=\"Data\">\n    <prismify language=\"javascript\">\n      private results = {\n        display: false,\n        data: []\n      };\n      onSearch(text: string) {\n        this.resultsHandler();\n      }\n      resultsHandler() {\n        // Some data actions\n        // Get data from service then add to results object below.\n        this.results = {\n          display: true,\n          data: [\n            {datum1: 'datum1'},\n            {datum2: 'datum2'}\n          ]\n        };\n      }\n    </prismify>\n  </gov-tab>\n\n</gov-tabset>\n\n<discuss-component issue=\"38\"></discuss-component>";

