	module.exports = "<h2>Gov Header</h2>\n\n<gov-tabset>\n\n  <gov-tab heading=\"HTML Example\">\n    <docs-example language=\"markup\">\n      <div id=\"skiplink-container\">\n        <div>\n          <a href=\"#content\" class=\"skiplink\">Skip to main content</a>\n        </div>\n      </div>\n      <div id=\"global-cookie-message\">\n        <p>GOV.UK uses cookies to make the site simpler. <a href=\"https://www.gov.uk/help/cookies\">Find out more about cookies</a></p>\n      </div>\n      <header role=\"banner\" id=\"global-header\" class=\"with-proposition\">\n        <div class=\"header-wrapper\">\n          <div class=\"header-global\">\n            <div class=\"header-logo\">\n              <a href=\"https://www.gov.uk\" title=\"Go to the GOV.UK homepage\" id=\"logo\" class=\"content\">GOV.UK</a>\n            </div>\n          </div>\n          <div class=\"header-proposition\">\n            <div class=\"content\">\n              <a href=\"#proposition-links\" class=\"js-header-toggle menu\">Menu</a>\n              <nav id=\"proposition-menu\">\n                <a href=\"/\" id=\"proposition-name\">Single Page Platform Development Kit</a>\n                <ul id=\"proposition-links\">\n                  <li>\n                    <a href=\"\">Layout</a>\n                  </li>\n                  <li>\n                    <a href=\"\">Content</a>\n                  </li>\n                  <li>\n                    <a href=\"\">Components</a>\n                  </li>\n                  <li>\n                    <a href=\"\">Examples</a>\n                  </li>\n                </ul>\n              </nav>\n            </div>\n          </div>\n        </div>\n      </header>\n      <div id=\"global-header-bar\"></div>\n    </docs-example>\n  </gov-tab>\n\n  <gov-tab heading=\"Angular Markup\">\n    <docs-example language=\"markup\">\n      Default View\n      <gov-header\n          heading=\"$ctrl.heading\"\n          nav-items=\"$ctrl.navItems\"\n          is-business=\"$ctrl.isBusiness\"\n          show-skip-link=\"$ctrl.showSkipLink\"\n          show-cookie=\"$ctrl.showCookie\"></gov-header>\n\n      2: MinimalView\n      <gov-header\n          heading=\"$ctrl.heading\"\n          nav-items=\"$ctrl.navItems\"\n          is-business=\"$ctrl.isBusiness\"\n          show-skip-link=\"$ctrl.showSkipLink\"\n          show-cookie=\"$ctrl.showCookie\"\n          title=\"Prosecute cases\"\n          minimal-view=\"$ctrl.minimalView\"></gov-header>\n\n      3: MinimalView (when user is on service home page)\n      <gov-header\n          heading=\"$ctrl.heading\"\n          nav-items=\"$ctrl.navItems\"\n          is-business=\"$ctrl.isBusiness\"\n          show-skip-link=\"$ctrl.showSkipLink\"\n          show-cookie=\"$ctrl.showCookie\"\n          title=\"Prosecute cases\"\n          minimal-view=\"$ctrl.minimalView\"\n          title-disabled=\"true\"></gov-header>\n\n      4: MinimalView (when the user is not signed in)\n      <gov-header minimal-view=\"$ctrl.minimalView\"></gov-header>\n\n    </docs-example>\n  </gov-tab>\n\n  <gov-tab heading=\"Data\">\n    <prismify language=\"javascript\">\n      heading: string = 'Single Page Platform Development Kit';\n      navItems = [\n        {\n          state: 'layout',\n          label: 'Layout'\n        },\n        {\n          state: 'content',\n          label: 'Content'\n        },\n        {\n          state: 'components',\n          label: 'Components'\n        },\n        {\n          state: 'examples',\n          label: 'Examples'\n        }\n      ];\n      navigation = {\n        mainContent: {\n          navigate: function () {\n            // navigate to main content\n          }\n        }\n      };\n      showCookie = false; // if true will display the cookie message over the header\n      showSkipLink = false, // if true will display 'Skip to main content' link\n      isBusiness = false; // if true the width will expand to 1170px\n      minimalView = true; if true will show the header in minimal view.\n      titleDisabled = true; if true will show the header in disabled mode.\n    </prismify>\n  </gov-tab>\n\n</gov-tabset>\n\n<discuss-component issue=\"8\"></discuss-component>\n";

