	module.exports = "<h2 class=\"heading-medium\">Right column navigation</h2>\n<p>Component for a column navigation</p>\n<p>Each component has an optional <code>metadata</code> attribute. Metadata is displayed at the top of the component. Metadata can either be passed in as an object (for a single metadata item), or as an array. Metadata can render a string of html (see example).</p>\n<p>Each component has a required <code>sections</code> attribute which takes an array of sections.</p>\n<p>Each section has an optional <code>header</code> string and a required list of <code>links</code>. Links can be made bold by adding an <code>isBold: true</code> property to the particular link object.</p>\n\n<gov-tabset>\n\n  <gov-tab heading=\"HTML Example\">\n    <docs-example language=\"markup\">\n      <div class=\"grid-row\">\n        <div class=\"column-third\">\n          <!-- related -->\n          <div class=\"related\">\n            <div class=\"related-section\">\n              <ul class=\"related-links\">\n                <li><a href=\"#\">View this user&rsquo;s details</a></li>\n                <li><a href=\"#\">Search for user in this organisation</a></li>\n                <li><a href=\"#\">Search for another organisation</a></li>\n                <li><a class=\"bold-xsmall\" href=\"#\">More</a></li>\n              </ul>\n            </div>\n          </div>\n          <!-- end -->\n        </div>\n      </div>\n    </docs-example>\n  </gov-tab>\n\n  <gov-tab heading=\"Angular Markup\">\n    <docs-example language=\"markup\">\n      <div class=\"grid-row\">\n        <div class=\"column-third\">\n          <gov-column-navigation sections=\"$ctrl.example1.sections\">\n          </gov-column-navigation>\n        </div>\n      </div>\n    </docs-example>\n\n    <docs-example language=\"markup\">\n      <div class=\"grid-row\">\n        <div class=\"column-third\">\n          <gov-column-navigation metadata=\"$ctrl.example2.column1.metadata\" sections=\"$ctrl.example2.column1.sections\">\n          </gov-column-navigation>\n        </div>\n        <div class=\"column-third\">\n          <gov-column-navigation metadata=\"$ctrl.example2.column2.metadata\" sections=\"$ctrl.example2.column2.sections\">\n          </gov-column-navigation>\n        </div>\n      </div>\n    </docs-example>\n\n    <docs-example language=\"markup\">\n      <div class=\"grid-row\">\n        <div class=\"column-third\">\n          <gov-column-navigation sections=\"$ctrl.example3.column1.sections\">\n          </gov-column-navigation>\n        </div>\n        <div class=\"column-third\">\n          <gov-column-navigation sections=\"$ctrl.example3.column2.sections\">\n          </gov-column-navigation>\n        </div>\n      </div>\n    </docs-example>\n  </gov-tab>\n  \n  <gov-tab heading=\"Data\">\n    <prismify language=\"javascript\">\n      example1 = {\n\n        sections: [\n          {\n            links: [\n              {\n                text: 'View this user\\'s details',\n                onClick: function() {\n                  console.log('Clicked \\\"Viewing the user\\'s details\\\"');\n                }\n              },\n              {\n                text: 'Search for user in this organisation',\n                onClick: function() {\n                  console.log('Clicked on \\\"Search for user in this organisation\\\"');\n                }\n              },\n              {\n                text: 'Search for another organisation',\n                onClick: function() {\n                  console.log('Clicked on \\\"Search for another organisation\\\"');\n                }\n              },\n              {\n                text: 'More',\n                isBold: true,\n                onClick: function() {\n                  console.log('Clicked on \\\"More\\\"');\n                }\n              }\n            ]\n          }\n        ]\n      };\n\n      example2 = {\n\n        column1: {\n          metadata: {\n            text: 'This user last signed in on 13 December 2015 at 2:36pm'\n          },\n          sections: [\n            {\n              links: [\n                {\n                  text: 'Search for another user',\n                  onClick: function() {\n                    console.log('Clicked on \\\"Search for another user\\\"');\n                  }\n                }\n              ]\n            }\n          ]\n        },\n\n        column2: {\n          metadata: [\n            {\n              text: 'This users account will be suspended on 17 March 2017'\n            },\n            {\n              text: 'This user last signed in on 13 December 2015 at 2:36pm'\n            }\n          ],\n          sections: [\n            {\n              links: [\n                {\n                  text: 'Search for another user',\n                  onClick: function() {\n                    console.log('Clicked on \\\"Search for another user\\\"');\n                  }\n                }\n              ]\n            }\n          ]\n        }\n      };\n\n      example3 = {\n\n        column1: {\n          sections: [\n            {\n              heading: 'Actions for this org',\n              links: [\n                {\n                  text: 'View organisation\\'s details',\n                  onClick: function() {\n                    console.log('Clicked on \\\"View organisation\\'s details\\\"');\n                  }\n                }\n              ]\n            },\n            {\n              heading: 'Other actions',\n              links: [\n                {\n                  text: 'Search for another organisation',\n                  onClick: function() {\n                    console.log('Clicked on \\\"Search for another organisation\\\"');\n                  }\n                },\n                {\n                  text: 'Search all users',\n                  onClick: function() {\n                    console.log('Clicked on \\\"Search all users\\\"');\n                  }\n                }\n              ]\n            }\n          ]\n        },\n\n        column2: {\n          sections: [\n            {\n              heading: 'To do',\n              links: [\n                {\n                  text: 'View case material',\n                  onClick: function() {\n                    console.log('Clicked on \\\"View case material\\\"');\n                  }\n                },\n                {\n                  text: 'Enter analysis and charging decisions',\n                  onClick: function() {\n                    console.log('Clicked on \\\"Enter analysis and charging decisions\\\"');\n                  }\n                },\n                {\n                  text: 'Create action plan',\n                  onClick: function() {\n                    console.log('Clicked on \\\"Create action plan\\\"');\n                  }\n                }\n              ]\n            }\n          ]\n        }\n      };\n    </prismify>\n  </gov-tab>\n\n</gov-tabset>\n\n<discuss-component issue=\"9\"></discuss-component>";

